.home__logo{height:7rem;margin:4rem auto}.productos__grid{display:flex;flex-direction:column;align-items:center;margin:4rem auto}@media(min-width: 768px){.productos{display:grid;gap:2rem;grid-template-columns:repeat(2, 1fr)}}.producto__imagen{width:auto;height:auto;object-fit:cover}.producto__titulo{font-size:2rem;text-align:left;margin:3.5rem 0 0 0;line-height:0;color:#ff5100}@media(min-width: 768px){.producto__titulo{font-size:2rem}}.producto__ubicacion{font-size:1.8rem;text-align:left;margin:0;color:#b4b1b1}@media(min-width: 768px){.producto__ubicacion{font-size:2rem}}.informaciones{display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start}.informaciones .informacion{display:flex}.informaciones .informacion .casa{display:block}.informaciones .informacion .casa__baño{height:5rem}@media(min-width: 768px){.informaciones .informacion .casa__baño{height:4rem;width:4rem}}.informaciones .informacion .casa__cantidad{text-align:center;margin:0;font-size:1rem;color:#777575;font-weight:700}@media(min-width: 768px){.informaciones .informacion .casa__cantidad{font-size:1.5rem}}.informaciones__precio{font-size:2rem;line-height:1.2;text-align:center;margin:1.8rem auto;color:#777575;font-weight:700}@media(min-width: 768px){.informaciones__precio{font-size:4.2rem;margin:.5rem auto}}.informaciones__mas img{height:4rem;margin:1rem auto}@media(min-width: 768px){.informaciones__mas img{height:5rem}}